.ServicesSection {
  .service {
    width: 95%;
    background-color: #e4e4e4;
    border-left: 1px solid #a4acc4;
    border-right: 1px solid #a4acc4;
    border-bottom: 1px solid #a4acc4;
    border-top: 8px solid rgba(164, 172, 196, 0.33);
    color: #313131;
    font-family: 'Nunito,sans-serif';
    font-size: 1.25rem;
    line-height: 1.75rem;
    transition: all .4s ease-in-out;
    @media screen and (max-width: 1400px) {
      width: 100%;
      margin-bottom: 1rem;
    }

    @media screen and (max-width: 1920px) {
      width: 80%;
      margin-bottom: 1rem;
    }
    &:hover {
      border-top: 8px solid #a4acc4;
    }

    .service-content {
      padding: 1rem;

      .s-title {
        font-size: 1.6rem;
        font-weight: 500;
        position: relative;
        padding-bottom: 1rem;
        margin: 1rem 0;
        font-family: 'Nunito,sans-serif';

        &::after {
          content: "";
          position: absolute;
          bottom: 0;
          left: 0;
          width: 30%;
          height: 4px;
          background-color: #cbced8;
        }
      }
    }
  }
}

.servives-container {
  display: flex;
  justify-content: space-between;
  margin-top: 4rem;
  @media screen and (max-width: 1400px) {
    flex-direction: column;
    margin-bottom: 2rem;
  }
}
