.App{
  .sidebar{
    width: 16%;
    height: 100vh;
    background-color: #a4acc4;
    position: fixed;
    z-index: 11;
    border-right: 7px solid #a4acc4;
    transform-origin: left;
    @media screen and (max-width: 1000px){
      transition: all .4s ease-in-out;
      transform: translateX(-100%);
      width: 30%;
    }
    @media screen and (max-width: 411px){
      width: 50%;
    }
  }
  .nav-toggle{
    @media screen and (max-width: 1000px){
      transition: all .4s ease-in-out;
      transform: translateX(0);
    }
  }

  .main-content{
    width: 84%;
    margin-left: 16%;
    background-color: rgba(255, 255, 255, 0.31);
    min-height: 100vh;
    background-image: url(../img/blogs/dots.svg);
    display: grid;
    position: relative;
    .content{
      margin: 5rem 15rem;
      @media screen and (max-width: 1100px){
        margin: 2rem 10rem;
      }
      @media screen and (max-width: 1400px){
        margin: 3rem 10rem;
      }
      @media screen and (max-width: 1400px){
        margin: 3rem 10rem;
      }
      @media screen and (max-width: 710px){
        margin: 2rem 3rem;
      }
    }
    @media screen and (max-width: 1000px){
      margin-left: 0;
      width: 100%;
    }
  }
}

.nav-btn{
  position: absolute;
  z-index: 10;
  right: 10%;
  top: 5%;
  width: 4rem;
  height: 4rem;
/*  display: none;*/
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  .lines-1, .lines-2, .lines-3{
    height: .4rem;
    width: 100%;
    background-color: #000000;
    pointer-events: none;
    display: none;
    border-radius: 20px;
    &:not(:last-child){
      margin-bottom: .5rem;
    }
  }
  @media screen and (max-width: 1000px){
    display: block;
    .lines-1, .lines-2, .lines-3{
      display: block;
    }
  }
}